import { AuthfakeauthenticationService } from './../../core/services/authfake.service';
import { Component, OnInit, AfterViewInit, ElementRef, ViewChild, Input, OnChanges } from '@angular/core';
import MetisMenu from 'metismenujs';
import { EventService } from '../../core/services/event.service';
import { Router, NavigationEnd, ActivatedRoute, NavigationStart, NavigationCancel } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { MenuItem } from './menu.model';
import { MATERIALMANAGEMENTMENU } from './material-management-sidemenu';
import { TranslateService } from '@ngx-translate/core';
import { SIDEMENU } from './side-menu';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})

/**
 * Sidebar component
 */
export class SidebarComponent implements OnInit, AfterViewInit, OnChanges {
  @ViewChild('componentRef') scrollRef;
  @Input() isCondensed = false;
  menu: any;
  data: any;

  menuItems = [];

  @ViewChild('sideMenu') sideMenu: ElementRef;

  constructor(private eventService: EventService, private router: Router, public translate: TranslateService, private http: HttpClient, public authfakeauthenticationService: AuthfakeauthenticationService, public activatedRoute: ActivatedRoute) {
    router.events.forEach((event) => {
      if (event instanceof NavigationEnd) {
        setTimeout(() => {
          this._activateMenuDropdown();
          this._scrollElement();
        }, 100)

      }
    });
  }

  ngOnInit() {
    this.authfakeauthenticationService.currentSideMenu.subscribe((res: any) => {
      this.initialize(res);
    })

    this._scrollElement();
  }

  ngAfterViewInit() {
    this.menu = new MetisMenu(this.sideMenu.nativeElement);
    setTimeout(() => {
      this._activateMenuDropdown();
    }, 100)

  }

  toggleMenu(event) {
    event.currentTarget.nextElementSibling.classList.toggle('mm-show');
  }

  ngOnChanges() {
    if (!this.isCondensed && this.sideMenu || this.isCondensed) {
      setTimeout(() => {
        this.menu = new MetisMenu(this.sideMenu.nativeElement);
      });
    } else if (this.menu) {
      this.menu.dispose();
    }
  }
  _scrollElement() {
    setTimeout(() => {
      if (document.getElementsByClassName("mm-active").length > 0) {
        const currentPosition = document.getElementsByClassName("mm-active")[0]['offsetTop'];
        if (currentPosition > 500)
          if (this.scrollRef.SimpleBar !== null)
            this.scrollRef.SimpleBar.getScrollElement().scrollTop =
              currentPosition + 300;
      }
    }, 300);
  }

  /**
   * remove active and mm-active class
   */
  _removeAllClass(className) {
    const els = document.getElementsByClassName(className);
    while (els[0]) {
      els[0].classList.remove(className);
    }
  }

  /**
   * Activate the parent dropdown
   */
  _activateMenuDropdown() {
    this._removeAllClass('mm-active');
    this._removeAllClass('mm-show');
    // localStorage.clear();
    let links: any = [];
    links = document.getElementsByClassName('side-nav-link-ref');
    // console.log('Links',links);

    let menuItemEl = null;
    // tslint:disable-next-line: prefer-for-of
    const paths = [];
    for (let i = 0; i < links.length; i++) {
      // console.log(links[i]['pathname']);
      const pathname = new URL(links[i]).hash;
      const cleanedPath = pathname.startsWith('#') ? pathname.substring(1) : pathname;
      paths.push(cleanedPath);
    }
    console.log('paths', paths);
    let x = this.router.url;
      if (x == '/material-management/viewfullgrid' || x =='/workarea' ) {
        var itemIndex = 1;
      }
      else if(x=='/patient-registration/register'){
        var itemIndex = 5;
      }
      else
      {
        var itemIndex = paths.indexOf(window.location.hash.substring(1));
      }
    // console.log('itemindex',itemIndex);
    if (itemIndex === -1) {
      const strIndex = window.location.pathname.lastIndexOf('/');
      const item = window.location.pathname.substr(0, strIndex).toString();
      menuItemEl = links[paths.indexOf(item)];
    } else {
      menuItemEl = links[itemIndex];
      // console.log('menuItemEl',menuItemEl);

    }
    if (menuItemEl) {
      menuItemEl.classList.add('active');
      const parentEl = menuItemEl.parentElement;
      // console.log('parentEL',parentEl);

      if (parentEl) {
        parentEl.classList.add('mm-active');
        const parent2El = parentEl.parentElement.closest('ul');
        if (parent2El && parent2El.id !== 'side-menu') {
          parent2El.classList.add('mm-show');
          const parent3El = parent2El.parentElement;
          if (parent3El && parent3El.id !== 'side-menu') {
            parent3El.classList.add('mm-active');
            const childAnchor = parent3El.querySelector('.has-arrow');
            const childDropdown = parent3El.querySelector('.has-dropdown');
            if (childAnchor) { childAnchor.classList.add('mm-active'); }
            if (childDropdown) { childDropdown.classList.add('mm-active'); }
            const parent4El = parent3El.parentElement;
            if (parent4El && parent4El.id !== 'side-menu') {
              parent4El.classList.add('mm-show');
              const parent5El = parent4El.parentElement;
              if (parent5El && parent5El.id !== 'side-menu') {
                parent5El.classList.add('mm-active');
                const childanchor = parent5El.querySelector('.is-parent');
                if (childanchor && parent5El.id !== 'side-menu') { childanchor.classList.add('mm-active'); }
              }
            }
          }
        }
      }
    }

  }

  /**
   * Initialize
   */
  initialize(data: any): void {
    // this.menuItems = MENU;
    switch (data) {
      case 'sidemenu': {
        this.menuItems = SIDEMENU;
        break;
      }
      case 'material-management': {
        this.menuItems = MATERIALMANAGEMENTMENU;
        break;
      }
    }

  }

  /**
   * Returns true or false if given menu item has child or not
   * @param item menuItem
   */
  hasItems(item: MenuItem) {
    return item.subItems !== undefined ? item.subItems.length > 0 : false;
  }

  navigateBack(data: any) {
    if (data == 'home') {
      this.authfakeauthenticationService.changeSideMenu('sidemenu');
      this.router.navigate(['/procedure']);
    }
  }
}
